.resume {
    margin: 0px 25% 5% 14%;
    padding: 10px;
    border-radius: 35px;
    background: #66cccc;
}

.resume-img {
    width: 100%;
    border-radius: 10px
    /* border: solid #A1A6B4 2px; */
}

.resume-link {
    text-decoration: none;
}

.resume-link:visited {
    color: #FFFFFF;
  }
  
.resume-link:active {
    color: #A1A6B4;
  }


