.contact {
    margin: 0px 25% 5% 14%;
    border-radius: 35px;
    padding: 25px;
    background: #66cccc;
}

.contact-me {
    text-align: center;
}

.input-header {
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
}

.input-field {
    width: 60%;
    height: 30px;
    border-radius: 5px;

}

.input-field::-webkit-input-placeholder {
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
}

input[type='text'] {
    font-size: 16px;
}

#message-field {
    height: 100px;
}

.submit-button {
    margin: 25px 10px 10px 10px;
    padding: 15px 25px 15px 25px;
    border-radius: 20px;
    background: #fe99ad;
    font-family: 'Acme', sans-serif;
    font-size: 24px;
    color: white;
}


