body {
  /* background: #535353; */
  /* font-family: 'Nunito', sans-serif; */
  background-image: url('./components/App/background.jpg');
  font-family: 'Poppins', sans-serif;
  color: #FFFFFF;
}

h1 {
  margin: 10px 2% 10px 2%;
  /* font-family: 'Space Mono', monospace; */
  font-family: 'Acme', sans-serif;
  font-size: 60px;
  /* border: solid red 5px; */
}

.nav-link {
  text-decoration: none;
  color: #FFFFFF;
}

.nav-link:visited {
  color: #FFFFFF;
}

.nav-link:active {
  color: #FEDC3D;
}

.pink {
  margin:0;
  padding:0;
  border: solid #fe99ad 5px;
  position: absolute;
  width: 100%;
}

nav {
  margin: 10px 2% 10px 2%;
  padding: 10px;
  /* font-family: 'Space Mono', monospace; */
  /* font-family: 'Acme', sans-serif; */
  /* border: solid yellow 5px; */
}

ul {
  margin: 10px 2% 10px 2%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 0px 0px 0px 30%;
  /* border: solid green 5px; */
}

li {
  margin: 10px 2% 10px 2%;
  padding: 10px 25px 10px 25px;
  /* font-family: 'Space Mono', monospace; */
  font-family: 'Acme', sans-serif;
  /* font-size: 36px; */
  list-style: none;
  /* border: solid blue 5px; */
}

h2 {
  font-size: 40px;
  /* font-family: 'Acme', sans-serif; */
}

footer {
  /* border: solid orange 5px; */
  margin: 98px 2% 10px 2%;
  padding: 10px 25px 10px 25px;
  text-align: right;
  position:fixed;
  bottom:0;
  left:0;
  right:0;
}

.logos {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  float: right;
  /* border: solid green 2px; */
}

.logo {
  padding: 15px 5px 15px 5px;
  height: 60px;
  /* justify-content: right; */
}

.react {
  font-size: 12px;
}


